@import url(https://fonts.googleapis.com/css2?family=Baloo+2:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:wght@400;600&display=swap);
html,
body {
  background-color: #ddeaee;
  margin: 0;
  padding: 0;
}
.Navbar {
  background-color: #c7cedf;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  padding: 5px;
}
.Navbar a, .logout-link {
  font-family: 'Baloo 2', cursive;
  text-decoration: none;
  color: #000;
  margin: 5px 10px;
  font-size: 20px;
  padding: 0 5px;
}

.home-link {
  width: 50px;
}
.home-link img {
  width: 100%;
}
.login-link, .logout-link {
  background-color: #dbb0a0;
  border-radius: 4px;
  box-shadow: 3px 3px 5px 1px rgb(131,105,83);

}
.signup-link {
  background-color: #eab586;
  border-radius: 4px;
  box-shadow: -3px -3px 5px 1px rgb(131,105,83);
}
.HomeItem {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.item-img {
  cursor: pointer;
  width: 170px;
  height: 160px;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 15px;
  box-shadow: 2px 2px 10px 3px #CFCFCF;
}
.item-img:hover {
  box-shadow: 8px 8px 40px 10px rgb(182, 223, 223);
}

.item-img img {
  max-width: 100%;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.item-img img:hover {
  max-width: 110%;
  width: 110%;
  max-height: 110%;
  height: 110%;
}

.Home {
  /* background-color: #068a9c; */
  height: 100vh;
  overflow: hidden;
}
.home-intro {
  padding: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.home-intro div {
  margin: 15px;
}
.intro-blurb {
  width: 32%;
}
.intro-blurb span {
  display: block;
}
.intro-blurb span:first-of-type {
  font-size: 50px;
}
.intro-blurb span:nth-child(2) {
  font-size: 20px;
}
.intro-img {
  width: 32%;
  display: -webkit-flex;
  display: flex;
  border-radius: 20px;
  box-shadow: 2px 2px 10px 3px #CFCFCF;
  overflow: hidden;
}
.intro-img img {
  width: 100%;
}

.home-layout {
  margin: 0 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.PageHeader {
  background-color: #068a9c;
  color: #fff;
  text-transform: uppercase;
  height: 150px;
  font-family: 'Josefin Sans', sans-serif;
  font-size: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.Nouser {
  display: -webkit-flex;
  display: flex;
  font-size: 20px;
  -webkit-justify-content: center;
          justify-content: center;
}

.Activity {
  
}
.activity-btns {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.activity-btns a {
  text-decoration: none;
  font-family: 'Baloo 2', cursive;
  border-radius: 10px;
  font-size: 18px;
  margin: 18px;
  font-weight: 500;
  padding: 5px;
  background-color: #fff;
}
.exercise-btn {
  color: #2b6684;
  border: 2px solid #2b6684;
}
.exercise-btn:hover {
  color: #fff;
  background-color: #2b6684;
}

.nutrition-btn { 
  color: #012e67;
  border: 2px solid #012e67;
}
.nutrition-btn:hover {
  color: #fff;
  background-color: #012e67;
}

.sleep-btn {
  color: #032e42;
  border: 2px solid #032e42;
}
.sleep-btn:hover {
  color: #fff;
  background-color: #032e42;
}
.aty-btn {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.aty-btn a {
  text-decoration: none;
  font-family: 'Baloo 2', cursive;
  border-radius: 10px;
  font-size: 18px;
  margin: 18px;
  font-weight: 500;
  padding: 5px;
  background-color: #fff;
}

.aty-section {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}
.aty-card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 360px;
  height: 200;
  padding: 16px 32px;
  background-color: #fff;
  border-radius: 20px;
  margin: 20px;
}
.aty-type {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  text-transform: uppercase;
  -webkit-align-items: center;
          align-items: center;
}
.aty-name {
  font-size: 25px;
}
.aty-category {
  border: 2px solid #000;
  font-size: 16px;
  padding: 2px 4px;
  border-radius: 6px;
}
.aty-stats {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.aty-stats > div {
  font-size: 20px;

}
.aty-stats > div > span {
  font-size: 18px;
}
.aty-duration {

}
.aty-intensity {

}
.aty-img {
  overflow: hidden;
  width: 80px;
  height: 80px;
  border-radius: 40px;
}
.aty-img img {
  width: 100%;
}
.sleep-btn {
  text-decoration: none;
  font-family: 'Baloo 2', cursive;
  border-radius: 10px;
  font-size: 18px;
  margin: 18px;
  font-weight: 500;
  padding: 5px;
  background-color: #fff;

  float: right;
}
.Signup {
  
}
.form-input-name {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 100%;
  margin: 10px;
}
.form-input-name .form-input {
  width: 45%;
  margin: 0;
}
.signup-btn {
  background-color: #eab586;
  padding: 5px 10px;
  margin: 10px;
  margin-bottom: 30px;
  font-size: 18px;
  font-family: 'Baloo 2', cursive;
  border-radius: 5px;
}
.signup-btn:hover {
  background-color: #e9c09c;
}
.Login {
}

.Login > div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.form, .form-fields {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.form-fields {
  width: 400px;
}
.form-input {
  max-width: 100%;
  margin: 10px;
  width: 100%;
}
label {
  display: block;
  font-size: 22px;
}
input {
  width: 100%;
  padding: 10px;
}
button {
  border: none;
  background: transparent;
  cursor: pointer;
}
.login-btn {
  background-color: #dbb0a0;
  padding: 5px 10px;
  margin: 10px;
  margin-bottom: 30px;
  font-size: 18px;
  font-family: 'Baloo 2', cursive;
  border-radius: 5px;
}
.login-btn:hover {
  background-color: #ebc0b1;
}
.error {
  font-family: 'Crimson Text', serif;
  color: #db9d00;
  font-weight: 600;
}





.App {
  font-family: 'Baloo 2', cursive;
}
